import {Injectable} from '@angular/core';
import {MessageService} from 'primeng/api';
import {AdaptMessage} from './toast.model';

@Injectable({
  providedIn: 'root'
})
export class AdaptMessageService extends MessageService {
  constructor() {
    super();
  }

  /**
   * Extension of Primeng method with AdaptMessage type
   * @since v10.14.0
   */
  add(message: AdaptMessage): void {
    super.add(message);
  }

  /**
   * Extension of Primeng method with AdaptMessage[] type
   * @since v10.14.0
   */
  addAll(messages: AdaptMessage[]): void {
    super.addAll(messages);
  }
}
